import { gsap } from 'gsap';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import SplitText from '../libraries/SplitText';

import LocomotiveScroll from 'locomotive-scroll';

export default function ScrollHome() {
   gsap.registerPlugin(ScrollTrigger);
   /*
      https://codepen.io/GreenSock/pen/ExPdqKy?editors=0010
   */

   const scroller = '.main-wrapper';

   const locoScroll = new LocomotiveScroll({
      el: document.querySelector(scroller),
      smooth: true,
      multiplier: .7,
      smartphone: {
         smooth: true
      }
   });

   locoScroll.on('scroll', ScrollTrigger.update);

   ScrollTrigger.scrollerProxy(scroller, {
      scrollTop(value) {
         return arguments.length ? locoScroll.scrollTo(value, 0, 0) : locoScroll.scroll.instance.scroll.y;
      },
      getBoundingClientRect() {
         return { top: 0, left: 0, width: window.innerWidth, height: window.innerHeight };
      },
      pinType: document.querySelector(scroller).style.transform ? 'transform' : 'fixed'
   });


   //Animations

   let tl = gsap.timeline();
   tl.to('.trees', { y: '50px', scale: 1.05 });

   ScrollTrigger.create({
      trigger: '.home-body-img-top',
      scrub: true,
      start: "100% 100%",
      scroller: scroller,
      animation: tl
   });

   let dottyTxt = new SplitText('.dotty-text', { type: 'chars' });
   dottyTxt = dottyTxt.chars.reverse();
   let dottyTl = gsap.timeline();
   dottyTl.from('.dotty', { duration: .5, scale: 1.08, ease: 'linear' })
      .to('.dotty', {
         duration: 1,
         left: '10%',
         scale: 1,
         ease: "power3.out"
      })
      .to(dottyTxt, {
         color: '#ffffff',
         ease: "power1.out",
         stagger: .2
      }, "<+.1")
      .to(dottyTxt, {
         duration: .8,
         x: -20,
         textShadow: '-4px 2px black',
         ease: "power1.out",
         stagger: .1
      }, "<")
      .to(dottyTxt, {
         duration: 1,
         textShadow: '-2px 1px black',
         stagger: .1,
         ease: "power1.out"
      });

   ScrollTrigger.create({
      trigger: '.dotty',
      start: 'top 90%',
      scroller: scroller,
      animation: dottyTl
   })

   let splitTxt = new SplitText('.home-body-img-bottom-p', { type: 'lines' });
   splitTxt = splitTxt.lines;
   let txtTl = gsap.timeline();
   txtTl.fromTo(splitTxt, {
      opacity: 0,
      y: -10
   }, {
      duration: 0.8,
      y: 0,
      opacity: 1,
      ease: "power2.in",
      stagger: .4
   });

   ScrollTrigger.create({
      trigger: '.home-body-img-bottom-p',
      start: 'top 100%',
      scroller: scroller,
      animation: txtTl
   })





   //Refresh!

   ScrollTrigger.addEventListener('refresh', () => locoScroll.update());
   ScrollTrigger.refresh();

   return locoScroll;
}
